#main-top-view-div {
    width: 100%;
    height: 800px;
    background: #202125;
    /* background-color: hotpink; */

}

#career-page-headings {
    padding-left: 100px;
    padding-top: 150px;
}

#career-page-headings h1 {
    color: #FFF;
    font-family: Poppins;
    font-size: 80px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    padding: 0;
    margin-bottom: 0;
}

#career-page-headings span {
    color: #FFB000;
    font-family: Poppins;
    font-size: 80px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

#career-page-headings p {
    color: #E5E7EB;
    /* font-family: Inter; */
    font-size: 28px;
    font-style: normal;
    font-weight: 400;
    line-height: 137.523%;
    width: 750px;
    padding: 0;
    margin-top: 40px;
    margin-bottom: 50px;
}

#career-page-headings button {       cursor: pointer;
    width: 283px;
    height: 74px;
    flex-shrink: 0;
    border-radius: 10px;
    background: #FFB000;
    border: none;
    outline: none;
    color: #FFF;
    /* font-family: Poppins; */
    font-size: 24px;
    font-style: normal;
    font-weight: 500;
    line-height: 137.523%;
    /* 33.005px */
}

#career-page-programs-abt-us {
    width: 185px;
    height: 120px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    padding: 10px 10px 10px 20px;
    justify-content: center;
    align-items: left;
    border-radius: 20px;
    border: 2px solid rgba(0, 0, 0, 0.2);
    background: linear-gradient(154deg,
            rgba(121, 117, 131, 0.2) 5.35%,
            rgba(54, 53, 103, 0.2) 83.85%),
        rgba(49, 48, 54, 0.3);
    position: absolute;
    right: 300px;
    bottom: 420px;
}

#career-page-programs-abt-us h1 {
    color: #fff;
    /* font-family: Poppins; */
    font-size: 35px;
    font-style: normal;
    font-weight: 700;
    line-height: 137.523%;
    margin-bottom: 5px;
}

#career-page-programs-abt-us span {
    color: #e5e7eb;
    /* font-family: Poppins; */
    font-size: 25px;
    font-style: normal;
    font-weight: 300;
    line-height: 137.523%;
}

#career-page-positions-abt-us {
    width: 200px;
    height: 120px;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
    padding: 10px 10px 10px 20px;
    justify-content: center;
    align-items: left;
    border-radius: 20px;
