    background: linear-gradient(154deg,
            rgba(121, 117, 131, 0.2) 5.35%,
            rgba(54, 53, 103, 0.2) 83.85%),
        rgba(49, 48, 54, 0.3);
    position: absolute;
    right: 104px;
    bottom: 250px;
    /* top:271px; */
}

#career-page-positions-abt-us h1 {
    color: #fff;
    /* font-family: Poppins; */
